<template>
    <!-- 创建封装容器 -->
    <div class="container" id="container">
        <!-- 注册页面 -->
		<div class="form-container sign-up-container">
            <el-form action="#">
                <h1>注册</h1>
                <div class="option">
                    <span>你的身份是:</span>
                    <select v-model="identity"  class="option2">
                        <option value="student" >学生</option>
                        <option value="teacher">教师</option>
                    </select>
                </div>
				<el-form :rules="loginrules" :model="loginform" ref="regFormRef">
<el-form-item prop="username">
                <el-input type="text" name="username" id="email" placeholder="用户名" v-model="loginform.username"></el-input>
</el-form-item>
</br>
<el-form-item prop="password">
                <el-input show-password name="password" id="password" placeholder="密码" v-model="loginform.password"></el-input>
</el-form-item>
</el-form>
                <el-form-item>
                <el-button type="primary" @click="reg">提交申请</el-button>
                </el-form-item>
            </el-form>
        </div>




        <!-- 登录页面 -->
        <div class="form-container sign-in-container">
            <el-form action="#"  :model="loginform" :rules="loginrules" ref="loginFormRef">
                <h1>登录</h1>

                <div class="option">
                    <span>你的身份是:</span>
                    <select v-model="identity"  class="option2">
                        <option value="student" >学生</option>
                        <option value="teacher" >教师</option>
                    </select>
                </div>
				
               <el-form-item prop="username">
                <el-input type="text" name="username" id="email" placeholder="用户名" v-model="loginform.username" ></el-input>
			  </el-form-item>  <!-- 用户名输入 -->
			  <el-form-item prop="password"><!-- 密码输入 -->
                <el-input  name="password" id="password" placeholder="密码" v-model="loginform.password" show-password></el-input>
              </el-form-item>  

                <a href="#" class="forget">忘记密码？</a>
                <el-form-item>
				<el-button type="primary" @click="login">登录</el-button>
                </el-form-item>
			</el-form>
        </div>

        <!-- 覆盖容器 -->
        <div class="overlay-container">
            <div class="overlay">
                <!-- 覆盖左边 -->
                <div class="overlay-penal overlay-left-container">
                    <!-- <h1>welcome back!</h1> -->
                    <p>
                        注册完成后，请点击下方按钮回到登录界面
                    </p>
                    <button @click="changeToL" class="ghost" id="signIn">登录</button>
                </div>

                <!-- 覆盖右边 -->
                <div class="overlay-penal overlay-right-container">
                    <!-- <h1>Hello Friend!</h1> -->
                    <p>
                        请未注册的新用户点击下面的按钮切换至注册界面
                    </p>
                    <button class="ghost" id="signUp" @click="changeToR">注册</button>
                </div>
            </div>
        </div>
    </div>
    
</template>

<script>
	import axios from 'axios'
  export default{
	  name:'login',
      data(){
          return{
              identity:'student',
      			  loginform:{
      				  username:'',//用户名
      				  password:'',//密码
      			  },// 登录表单的数据绑定
				  uid:0,
				    identity:'',
      			  loginrules:{
      				  username:[
      					  { required: true, message: '请输入用户名', trigger: 'blur' },
      					  { min: 6, max: 9, message: '长度在 6到 9个字符', trigger: 'blur' }
      					       ],//用户名验证
      				  password:[
      					  { required: true, message: '请输密码', trigger: 'blur' },
      					  { min: 8, message: '长度在大于8个字符', trigger: 'blur' }
      				  ]//密码验证
      			  }//这是表单的验证规则
          }
        },
        methods:{
            changeToR:function(){
                 container.classList.add('penal-right-active');
            },
      
            changeToL:function(){
                 container.classList.remove('penal-right-active');
            },
      			login(){
					if(this.identity==="student"){
      				this.$refs.loginFormRef.validate(async valid =>{
						
						
      					if(valid===false){
							return;
						}

					const{data:res} = await this.$http.post("http://10.122.215.247:8080/user/loginstu",this.loginform);
					//发送登录请求
      					if(res.msg==="no")return this.$message.error('登录失败')
						this.uid=res.data.uid
						this.$message.success('登陆成功')//登录判断
					     this.$store.commit('loguser',this.loginform.username)
						 this.$store.commit('loguid',this.uid)
						  this.$store.commit('logpass',this.loginform.password)
					//	window.sessionStorage.setItem("token",res.data.token);//保存token
// console.log(this.$store.state.username)
// console.log(this.$store.state.password)
// console.log(this.$store.state.uid)

					     this.$router.push('/home');
						
      				})}
				
					if(this.identity==="teacher"){
						this.$refs.loginFormRef.validate(async valid =>{
							if(valid===false){
								return;
							}
						
						const{data:res} = await this.$http.post("http://10.122.215.247:8080/user/logintea",this.loginform);
						
						//发送登录请求
							if(res.msg==="no")return this.$message.error('登录失败')
							this.$message.success('登陆成功')//登录判断
						//	window.sessionStorage.setItem("token",res.data.token);//保存token

                        this.uid=res.data.uid
					     this.$store.commit('loguser',this.loginform.username)
						 this.$store.commit('loguid',this.uid)
						  this.$store.commit('logpass',this.loginform.password)


                        this.$router.push('/teahome')
							
						})
					}
      			},
				//注册函数，注册与登录用的是同一组变量，当注册成功后可直接登录
				reg(){
					if(this.identity==="student"){
					this.$refs.regFormRef.validate(async valid =>{
						if(valid===false){
							return;
						}
						const{data:res} = await this.$http.post("http://10.122.215.247:8080/user/addstu",this.loginform);
						if(res.msg==="no")return this.$message.error('用户名重复')
						this.$message.success('注册成功')
					})}
					if(this.identity==="teacher"){
						this.$refs.regFormRef.validate(async valid =>{
							if(valid===false){
								return;
							}
							const{data:res} = await this.$http.post("http://10.122.215.247:8080/user/addtea",this.loginform);
							if(res.msg==="no")return this.$message.error('用户名重复')
							this.$message.success('注册成功')
						})
					}
				},
				
					
						
			
				
      			},
				
    };
</script>

<style scoped>
* {
    padding:0;
    margin:0;
    box-sizing: border-box;
}

body{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    /* background-image:url(); */
    /* background: url("../assets/login_in.png") no-repeat; */
}


button {
    outline: none;
    border: 1px solid #FF4B2B;
    border-radius: 20px;
    background-color: #FF4B2B;
    padding: 10px 40px;
    font-size: 12px;
    text-transform: uppercase;
    color: white;
    transition: transform 85ms ease-in;
}
h1 {
    text-transform: capitalize;
    font-size: 30px;
}
p{
    margin: 10px 0 30px;
}
button:active {
    transform: scale(.95);
}
.ghost {
    background-color: transparent;
    border-color: darkgray
}


.container{
    position: absolute;
    top: 55%;
    left: 50%;
    transform: translate(-50%,-50%);
    width: 768px;
    max-width: 100%;
    min-height: 480px;
    background:linear-gradient(45deg, black, transparent);
    box-shadow: 0 14px 28px rgba(0, 0, 0, .25), 0 10px 10px rgba(0, 0, 0, .22);
    border-radius: 10px;
    overflow: hidden;
}

.form-container{
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background-image:url() ; */
    background: gray;
    height: 100%;
    width: 50%;
    padding: 0 40px;
    transition: all .65s ease-in-out;
}
.form-container form{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.form-container input {
    width: 100%;
    height: 40px;
    margin: 8px 0;
    background-color: #EEEEEE;
    border: none;
    font-size: 14px;
    padding: 0 20px;
    border-radius: 50px;
}
.sign-in-container{
    z-index: 10;
}
.sign-up-container{
    z-index: 5;
}

.social-container {
    display: flex;
}
.social-container a{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: 1px solid #ddd;
    line-height: 40px;
    text-align: center;
    color: #333;
    margin: 20px 8px;
}
.social-container a:hover {
    background-color: #222F3E;
    color: white;
    border-color: #CBCBCC;
}

.overlay-container {
    position: absolute;
    right: 0;
    width: 50%;
    height: 100%;
    background:linear-gradient(45deg, black, transparent);
    overflow: hidden;
    transition: transform .65s ease-in-out;
    z-index: 100;
}
.overlay {
    position: absolute;
    display: flex;
    left: -100%;
    width: 200%;
    height: 100%;
    background: linear-gradient(45deg, black, transparent);
}

.overlay-penal {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 50%;
    padding: 0 40px;
    text-align: center;
    color: white;
    font-size: 12px;
}
.forget {
    height: 20px;
    color: black;
    text-decoration: none;
    margin: 5px 0 15px;
    font-size: 12px;
    text-transform: capitalize;
}
.forget:hover {
    border-bottom: 2px tomato solid;
}

.overlay-left-container {
    transform: translateX(10%);
    transition: transform .65s ease-in-out;
}
.overlay-right-container {
    transform: translateX(0);
    transition: transform .65s ease-in-out;
}

.container.penal-right-active .sign-in-container {
    z-index: 5;
    transform: translateX(100%);
}

.container.penal-right-active .sign-up-container {
    z-index: 10;
    transform: translateX(100%);
}

.container.penal-right-active .overlay-container {
    transform: translateX(-100%);
}
.container.penal-right-active .overlay {
    transform: translateX(50%);
}
.container.penal-right-active .overlay-left-container {
    transform: translateX(0);
}
.container.penal-right-active .overlay-right-container {
    transform: translateX(-10%);
}

.option{
    /* padding: 20px 0; */
    margin: 10px;
    padding: 10px;
    font-size: 14px;
    width: 304px;
    background: white;
    border-radius: 50px;
}

.option2{
    font-size: 15px;
    width: 180px;
}

</style>