<template>
	<div>
		<el-card >
		  <div slot="header" class="clearfix">
		    <span>课程</span>
		    <el-button style="float: right; padding: 3px 0" type="text" @click="add()">添加课程</el-button>
		  </div>
		
		<el-table :data="classlist" border stripe>
			<el-table-column label="课程id" prop="cid"></el-table-column>
			<el-table-column label="教师id" prop="uid"></el-table-column>
			<el-table-column label="学生数目" prop="stunum"></el-table-column>
			<el-table-column label="课程名" prop="name"></el-table-column>
			<el-table-column label="创建日期" prop="date"></el-table-column>
			<el-table-column label="退出课程">
				<template slot-scope="scope">
					<el-tooltip effect="dark" content="退出课程" placement="top" :enterable="false">
				<el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteclass(scope.row.cid)"></el-button>
					</el-tooltip>			
					
					</template>
				
			</el-table-column>
			<el-table-column label="查看题目">
				<template slot-scope="scope">
					<el-tooltip effect="dark" content="查看题目" placement="top" :enterable="false">
				<el-button type="primary" size="mini" @click="detail(scope.row.cid)">查看题目</el-button>
					</el-tooltip>			
					
					</template>
				
			</el-table-column>
		</el-table>
		
		  
		</el-card>
		
		<el-dialog
		  title="加入课程"
		  :visible.sync="changeview"
		  width="50%">
		  <el-form  label-width="70px">
		    <el-form-item label="课程id" prop="cid">
		      <el-input v-model="cid"></el-input>
		    </el-form-item>
			</el-form>
		  <span slot="footer" class="dialog-footer">
		    <el-button @click="changeview = false">取 消</el-button>
		    <el-button type="primary" @click="addclass()">确 定</el-button>
		  </span>
		</el-dialog>
		
	  </div>
</template>
<script>
	export default {
	  data(){
		  return{
		   cid:null,
		   classlist:[],
		   changeview:false
		   
		  }
	  }	,
	  computed: {
	  	uid(){
			return this.$store.state.uid
		}
	  },
	  methods:{
		async addclass(){
			const {data:res} = await this.$http.post('http://10.122.215.247:8080/class/addstc',{cid:this.cid,uid:this.uid})
			if(res.msg!=="ok"){this.$message.info("该课程不存在")}
			this.$message.success('加入课程成功')
			this.changeview=false
			this.getclass()
		},
		add(){
			this.changeview=true
			
		},
	async getclass(){
			const {data:res} = await this.$http.post('http://10.122.215.247:8080/class/getcos',{uid:this.uid})
			if(res.msg!=="ok"){this.$message.info("拉取课程失败，请重新登录")}
			this.classlist=res.data
		},
	async	deleteclass(cid){
			
			const result  = await this.$confirm('将要退出该课程, 是否继续?', '提示', {
			       confirmButtonText: '确定',
			       cancelButtonText: '取消',
			       type: 'warning'
			     }).catch(err=>{
					return err
					})
		// 确认返回confirm，取消返回cancle
		    if(result!=='confirm'){
			return this.$message.info('取消删除')}
			await this.$http.post('http://10.122.215.247:8080/class/delsfc',{cid:cid,uid:this.uid})
			this.$message.success('删除成功')
			
			this.getclass()
		},
		detail(cid){
			
		 this.$store.commit('detailcid',cid)
		  this.$router.push('/questionlist')
		}
		
	  },
	  created(){
		  this.getclass()
	  }
	
	}
</script>

<style scoped>
	.el-breadcrumb{
		margin-bottom: 15px;
	}
	.el-card{
		box-shadow: 0 1px 1px rgba(0,0,0,0.15) !important;
	}
</style>
