<template>
    <div>
        <el-dialog
        title="提示"
        :visible.sync="dialogVisible"
        width="30%">
        <span>请输入课程名字</span>
        <el-input v-model="classname"></el-input>
        <span slot="footer" class="dialog-footer">
            <el-button @click="cancel">取 消</el-button>
            <el-button type="primary" @click="addclass">确 定</el-button>
        </span>
        </el-dialog>

        <el-tabs type="border-card">
            <el-tab-pane label="教学课程">
                <div class="youclass">
                     <el-table
                        :data="tableData"
                        style="width: 100%">
                        <el-table-column
                            prop="cid"
                            label="课程ID"
                            width="100">
                        </el-table-column>
                        
                       <el-table-column
                            prop="name"
                            label="学科"
                            width="180">
                        </el-table-column>
                        <el-table-column
                            prop="date"
                            label="创建日期"
                            width="180">
                        </el-table-column>
                        <el-table-column
                            prop="stunum"
                            label="课程人数">
                        </el-table-column>

                        <el-table-column
                        fixed="right"
                        label="操作"
                        width="130">

                        <template slot-scope="scope">
                            <!-- <el-button type="text" size="small">
                                <el-link icon="el-icon-edit">编辑</el-link>
                            </el-button> -->

                            <!-- <el-button @click="handleClick(scope.row)" type="text" size="small"> -->
                            <el-button @click="linkto(scope.row.uid,scope.row.cid)" type="text" size="small">
                                 <!-- <el-button @click="linkto(111,111)" type="text" size="small"> -->
                                <el-link icon="el-icon-view">查看课程内题目</el-link>
                                <!-- 查看课程 -->
                            </el-button>
                            
                        </template>

                        </el-table-column>
                        </el-table-column>
                    </el-table>
                </div>
            </el-tab-pane>

            <el-tab-pane label="课程管理">
                <div class="youclass">
                    <!-- <el-button type="primary" size="medium" @click="addclass">添加课程</el-button> -->
                    <el-button type="primary" size="medium" @click="dialogVisible = true">添加课程</el-button>
                        <el-table
                        :data="tableData"
                        style="width: 100%">

                        <el-table-column
                            prop="cid"
                            label="课程ID"
                            width="100">
                        </el-table-column>
                        
                       <el-table-column
                            prop="name"
                            label="学科"
                            width="180">
                        </el-table-column>
                        <el-table-column
                            prop="date"
                            label="创建日期"
                            width="180">
                        </el-table-column>
                        <el-table-column
                            prop="stunum"
                            label="课程人数">
                        </el-table-column>

                        <el-table-column
                        fixed="right"
                        label="操作"
                        width="120">
                        <template slot-scope="scope">
                            <el-button
                            @click="delclass(scope.row.name,scope.row.cid)"
                            type="text"
                            size="small">
                            <el-link icon="el-icon-delete">移除课程</el-link>
                            <!-- 移除课程 -->
                            </el-button>
                        </template>
                        </el-table-column>
                       </el-table>

                </div>
            </el-tab-pane>
        </el-tabs>
        <!-- <lookquestion :getuid="nextuid" 
                      :getcid="nextcid">
        </lookquestion> -->
    </div>
</template>

<script>
    export default{
        name:'yourclass',

        data() {
            return {
                // nextuid:111,//用于将uid和cid 传递给子组件
                // nextcid:111,

                teacheruid:this.$store.state.uid,//创建新课程要用到的 老师uid和 课程名字
                classname:'',

                dialogVisible: false,

                activeName: 'one',
                count: 0,

                classform:{
                    cid:0,//课程ID
                    uid:0,//教师ID
                    stunum:0,//学生人数
                    name:'',//课程名字
                    date:'',//创建时间

                    // subject: '',
                    // data: '',
                    // number: 0
                } ,
                tableData: [{classform:this.classform}]
            };
        },
        
        computed: {
			//由于计算属性时单项的，所以对于V-model双向绑定来说缺少一个方向的传值会报错
			username() {
				return this.$store.state.username
                // return 
			},
			uid(){
				return this.$store.state.uid
                // return 1

                // teacheruid = ?
			},
		},

        methods: {
            // addclass(){
            //     this.$router.push('addclass')
            // },

            //取消操作
            cancel(){
                this.dialogVisible = false
                this.classname=''
            },

            //添加课程
           async addclass(){
                await this.axios.post('http://10.122.215.247:8080/class/add',{name:this.classname ,uid:this.teacheruid })
                this.$router.push('yourclass')
                // this.$router.go(0)
                this.getusermsg()
                this.dialogVisible = false
                this.classname=''
                

                
            },

            //删除课程
            async delclass(cname,cid){
                // axios.post('http://10.122.215.247:8080/class/del',{name:cname  ,cid:cid })
               await this.axios.post('http://10.122.215.247:8080/class/del',{name:cname ,cid:cid })


                this.$router.push('yourclass')
                // this.$router.go(0)
                this.getusermsg()
                
            },

            // scope.row.uid教师ID      scope.row.cid课程ID
            linkto(uid,cid){
                // this.nextuid = uid //将uid和cid赋值给 nextuid 和nextcid,以便传递给子组件用于发送请求渲染界面
                // this.nextcid = cid 
                // this.$router.push('lookquestion')

                this.$router.push({path:'lookquestion',query:{uid,cid}})
                // console.log(nextuid)
            },

            async getusermsg(){
			// console.log(this.$store.state.uid)
			// console.log(this.$store.state.username)
			const {data:res} = await this.axios.post('http://10.122.215.247:8080/class/getcot',{uid:this.uid})
			// console.log(1)
			console.log(res.data)
			this.classform=res.data
			this.tableData = this.classform
			this.$set(this.tableData,this.classform)
			// console.log(this.list[0].username)
			// console.log(this.list[0])
			},
        },
        created(){
            this.getusermsg();
        }
    }

</script>

<style>
.youclass{
    height: 585px;
    overflow:auto ;
}
</style>