import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

const store = new Vuex.Store({
    state: {
       // 定义一个name，以供全局使用
        username: '',
        // 定义一个number，以供全局使用
		password:'',
        uid:0,
		cid:0,
		qid:0
        // 定义一个list，以供全局使用
    },
	 mutations:{
	
		changepass(state,pass){
			
			state.password=pass
		},
		loguser(state,msg){
								
			state.username=msg
		},
		logpass(state,msg){
			
			state.password=msg
		},
		loguid(state,msg){
				
			state.uid=msg
		},
		detailcid(state,msg){
			state.cid=msg
		},
		detailqid(state,msg){
			state.qid=msg
		}
		
		
	}
})

export default store