<template>
    <div id="checkquestion0">
        <div>
            <el-page-header @back="goBack" content="题目详情">
            </el-page-header>
        </div>

        <div>
            <!-- 标题 -->
            <div >
                <h2 id="myown">{{infor.qname}}</h2>
            </div>

            <!-- 关于题目  start -->
            <div >
                问题描述:
                <el-input
                :readonly="true"
                type="textarea"
                autosize
                v-model="infor.description">
                </el-input>
            </div>
            <div >
                输入格式:
                <el-input
                :readonly="true"
                type="textarea"
                autosize
               
                v-model="infor.inputformat">
                </el-input>
            </div>
            <div >
                输出格式:
                <el-input
                :readonly="true"
                type="textarea"
                autosize
              
                v-model="infor.outputformat">
                </el-input>
            </div>
			<div >
			    一组输入样例:
			    <el-input
			    :readonly="true"
			    type="textarea"
			    autosize
			    v-model="infor.samplein">
			    </el-input>
			</div>
			
            <div >
                一组输出样例:
                <el-input
                :readonly="true"
                type="textarea"
                autosize
                v-model="infor.sampleout">
                </el-input>
            </div>

            <div >
                你的代码:
                <el-input
                type="textarea"
                 :autosize="{ minRows: 16, maxRows: 16}"
                placeholder="请输入内容"
                v-model="code">
                </el-input>
            </div>

            
            <div id="mybutton">
                <el-button slot="reference" size="small" type="primary" @click="checksub">查看上次提交结果</el-button>
                <el-button slot="reference" size="small" type="primary" @click="onsubmit">提交代码</el-button>
            </div>
            
        </div>
		
		<el-dialog
		  title="提交结果"
		  :visible.sync="change"
		  width="55%">
		 <el-card >
			 
			 <div v-for="o in 5" :key="o" class="text item">
			     {{'测试点'+o+'：' + result[o-1] }}
			   </div>
			   总分：{{score}}
			 
		 </el-card>
		</el-dialog>
		
		
		<el-dialog
		  title="提交结果"
		  :visible.sync="change1"
		  width="55%">
		 <el-card class="box-card">
			{{testinfo}}
			 
		 </el-card> 
		</el-dialog>
		
    </div>
</template>

<script>
    export default{
        name:'checkquestion',
        data(){
            return{
                
			infor:{	
				qname:'',
				description:'',
				inputformat:'',
				outputformat:'',
				samplein:'',
				sampleout:'',
				
				},
				code:'',
                sid:null,
                change:false,
				change1:false,
				testinfo:'',
				result:[],
				score:0
            }
        },
		
		
		methods:{
		    goBack() {
		        this.$router.go(-1)
		        // console.log('go back');
		    },
			async onsubmit(){
			const{data:res}	=await  this.$http.post('http://10.122.215.247:8080/sub/add',{qid:this.qid,uid:this.uid,code:this.code})
				this.$message.success('提交成功，5秒后提示结果')
				this.sid=res.data							
				
				setTimeout(() => {
				        this.checksub()
				     }, 5000);
			},
		async	getqdetail(){
				 console.log(this.qid)
		   const{data:res} =await this.$http.post('http://10.122.215.247:8080/que/getsinfo',{qid:this.qid})
			  this.infor= res.data
			  
		},
		async checksub(){
					
			if(this.sid!==null){
				console.log(this.sid)
			
			const{data:res}= await this.$http.post('http://10.122.215.247:8080/sub/getinfo',{sid:this.sid})
			
			
			if(res.data.ischeck===0){
				this.$message.info('服务器繁忙，请再次提交代码，请勿退出')
		         	return
			}
			if(res.data.ischeck===1){
				this.change1=true
				this.testinfo=res.data.testinfo
			}
			
			if(res.data.ischeck===2){
				this.change=true
				this.testinfo=res.data.testinfo
				this.score=res.data.totalscore
				this.result=this.testinfo.split('/')
				console.log(this.result)
			}
			
			
			}
			else{
				this.$message.info('请先提交代码再查看')
			}
		}
			
			
		},
		computed: {
			qid() {
				return this.$store.state.qid 
			},
			uid() {
				return this.$store.state.uid 
			},
			
		},
		created(){
			this.getqdetail()
		},
    }

</script>

<style scoped>
#checkquestion0{
    height: 656px;
    overflow: auto;
    text-align: left;
}
#myown{
    margin-left: 25%;
}

#mybutton{
    margin-left: 40%;
}
.text {
    font-size: 14px;
  }

  .item {
    padding: 18px 0;
  }

  .box-card {
    width: 480px;
  }
</style>