<template>
    <div class="selfinfo" >
        

        <div class="myinfo">

            <el-input placeholder="请输入内容" v-model="editform.username" :disabled="true">
                
                <template slot="prepend">
                    <i slot="prefix" class="el-input__icon el-icon-user-solid"></i>
                    用户名
                </template>
            </el-input>

            <el-input placeholder="请输入内容" v-model="identity" :disabled="true">
                <template slot="prepend">
                    <i slot="prefix" class="el-input__icon el-icon-s-check"></i>
                    身份
                </template>
            </el-input>

            <el-input placeholder="请输入内容" v-model="editform.password"  :disabled="true">
                <template slot="prepend">
                    <i slot="prefix" class="el-input__icon el-icon-unlock"></i>
                    密码
                </template>
            </el-input>

            <div :hidden="choice">
                <!-- <el-input placeholder="请输入内容" v-model="oldpassword" show-password>
                    <template slot="prepend">
                        <i slot="prefix" class="el-input__icon el-icon-unlock"></i>
                        输入原密码
                    </template>
                </el-input> -->

                <el-input placeholder="请输入内容" v-model="editform.repass" show-password>
                    <template slot="prepend">
                        <i slot="prefix" class="el-input__icon el-icon-unlock"></i>
                        输入新密码
                    </template>
                </el-input>

                <!-- <el-input placeholder="请输入内容" v-model="againpassword" show-password>
                    <template slot="prepend">
                        <i slot="prefix" class="el-input__icon el-icon-unlock"></i>
                        确认新密码
                    </template>
                </el-input> -->
            </div>

            <div id="ownbutton">


                <div :hidden="!choice">
                    <el-popconfirm
                    confirm-button-text='确定'
                    confirm-button-type="Text"

                    cancel-button-text='取消'
                    cancel-button-type="primary"

                    icon="el-icon-info"
                    icon-color="red"
                    @confirm="zhuxiao"
                    title="注销后账号将不存在，确定要注销这个账号吗？">
                        <el-button slot="reference" size="small" >注销账号</el-button>
                    </el-popconfirm>

                    <el-button type="primary" size="small" @click="changepassword">重置密码</el-button>
                </div>

                <div :hidden="choice">
                    <el-button type="primary" size="medium" @click="quitchange">取消重置</el-button>
                    <!-- <el-button type="primary" size="small" @click="submitpassword">提交</el-button> -->
                    <el-button slot="reference" type="Text" size="small" @click="submitpassword">提交</el-button>
                </div>
            </div>
        
        </div>

    </div>
</template>

<script>
import axios from 'axios'
    export default{
        name:'selfinfo',
        created(){
            this.getusermsg();
        },


        data() {

            return {
            repassurl:'http://10.122.215.247:8080/user/rpwtea',

            // user:{
            //     username: 'luohanyi',
            //     password:'123456789',
            //     repass:'',
            // },

            editform:{
				uid:0,
				username:'',
				password:'',
				repass:'',
			},
			list:[{editform:this.editform}],
           
            // oldpassword:'',
            identity:'教师',
            choice:true,
             
            // againpassword:'',
            }
        },

        computed: {
			//由于计算属性时单项的，所以对于V-model双向绑定来说缺少一个方向的传值会报错
			username() {
				return this.$store.state.username; 
			},
			uid(){
				return this.$store.state.uid; 
			},
		},

        methods:{
            changepassword(){
                this.choice=false
            },
            quitchange(){
                this.choice=true
                // this.$router.go(0)
                this.editform.repass=''

            },

            async zhuxiao(){
                await this.axios.post('http://10.122.215.247:8080/user/deltea',{uid:this.uid,username:this.username})
                // const{data:res}  =	await this.$http.post('http://10.122.215.247:8080/user/delstu',this.editform)//此处可用delet请求	

                this.$message.success('删除成功')
                this.$router.push('/login')

            },

            async submitpassword(){
				// this.$refs.editformref.validate(async valid=>{
				// 	if(!valid)return
				// 	//通过则发起修改情求
				// const{data:res} = await this.axios.post(this.repassurl,this.editform)
				// })

				// this.$store.commit('changepass',this.editform.repass)
                await this.axios.post(this.repassurl,this.editform)
                
                this.choice=true
				//刷新
                this.$message.success('修改成功')
				this.getusermsg()
				//提示成功
				

                // this.$router.go(0)
            },

            async getusermsg(){

			// console.log(this.$store.state.uid)
			// console.log(this.$store.state.username)
			const {data:res} = await this.$http.post('http://10.122.215.247:8080/user/gettea',{username:this.username,uid:this.uid})
			// console.log(1)
			this.editform=res.data
			this.list[0] = this.editform
			this.$set(this.list,0,this.editform)
			},

        }
        
    }

</script>

<style scoped>
.selfinfo{
    box-sizing: border-box;
    height: 556px;
}

.myinfo{
    width: 400px;
    margin-left: 100px;
    margin-top: 100px;
}

#ownbutton{
    margin-top: 20px;
}

/* .zhuxiao{
    position: relative;
    left: 100px;
    top: -100px;
} */

</style>