<template>
	<div>
	<!-- 头部 -->
	<el-breadcrumb separator-class="el-icon-arrow-right">
	  <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
	  <el-breadcrumb-item>我的</el-breadcrumb-item>
	</el-breadcrumb>
	<!-- 卡片 -->
	<el-card>
	  <el-table :data="list">
		  <el-table-column label="用户名" prop="username"></el-table-column>
		  <el-table-column label="uid" prop="uid"></el-table-column>
		  <el-table-column label="学习积分" prop="exp"></el-table-column>
		<!--  <el-table-column label="经验值" prop="exp"></el-table-column> -->
		  <el-table-column label="修改信息">
			  <template slot-scope="scope">
			  <el-button type="primary" icon="el-icon-edit" size="mini" @click="changemsg()"></el-button>
			  <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteuser()"></el-button>
			  </template>
		  </el-table-column>
	  </el-table>
	</el-card>
	<!-- 修改对话框 -->
	<el-dialog
	  title="修改密码"
	  :visible.sync="change"
	  width="50%">
	  <el-form :model="editform" :rules="rules" ref="editformref" label-width="70px">
	    <el-form-item label="密码" prop="password">
	      <el-input v-model="editform.repass"></el-input>
	    </el-form-item>
		</el-form>
	  <span slot="footer" class="dialog-footer">
	    <el-button @click="change = false">取 消</el-button>
	    <el-button type="primary" @click="changeinfo()">确 定</el-button>
	  </span>
	</el-dialog>
	</div>
</template>

<script>
	import Vue from 'vue'
	export default {
		
		name:'mymsg',
		data(){
			return{
				
				//数据	
			//username:'',
			//uid:0,
            identity:'',
			change: false,
			// 用户信息对象
			editform:{
				uid:0,
				username:'',
				password:'',
				repass:'',
				exp:6
			},
			list:[{editform:this.editform}]
			,
			
			rules:{
				password:[
					{required:true,message:'请输入新密码',trigger:'blur'},
					{ min: 8, message: '长度在大于8个字符', trigger: 'blur' }
				
				]
			}
			}
		},
		computed: {
			//由于计算属性时单项的，所以对于V-model双向绑定来说缺少一个方向的传值会报错
			username() {
				return this.$store.state.username; 
			},
			uid(){
				return this.$store.state.uid; 
			},
		},
		
		methods: {
		 async getusermsg(){
			console.log(this.$store.state.uid)
			console.log(this.$store.state.username)
			const {data:res} = await this.$http.post('http://10.122.215.247:8080/user/getstu',{username:this.username,uid:this.uid})
			// console.log(1)
			console.log(res)
			this.editform=res.data
			this.list[0] = this.editform
			this.$set(this.list,0,this.editform)
			console.log(this.list[0].username)
			console.log(this.list[0])
			
			
			},
			// 弹出对话框
			 changemsg(){
				this.change=true
			},
			changeinfo(){
				//提交
				this.$refs.editformref.validate(async valid=>{
					if(!valid)return
					//通过则发起修改情求
				const{data:res} = await this.$http.post('http://10.122.215.247:8080/user/rpwstu',this.editform)
				})
				this.$store.commit('changepass',this.editform.repass)
				//关闭
				this.change=false
				//刷新
				this.getusermsg()
				//提示成功
				this.$message.success('修改成功')
			},
			
		  async	deleteuser(username){
				
				   const result  = await this.$confirm('此操作将永久删除该用户, 是否继续?', '提示', {
				          confirmButtonText: '确定',
				          cancelButtonText: '取消',
				          type: 'warning'
				        }).catch(err=>{
							return err
						})
						if(result!=='confirm'){
							return this.$message.info('取消删除')}
						
			const{data:res}  =	await this.$http.post('http://10.122.215.247:8080/user/delstu',this.editform)//此处可用delet请求	
				this.$message.success('删除成功')
				this.$router.push('/login')
			
			
			
			
		},
	},
	created(){
		this.getusermsg();
	}
}
</script>

<style scoped>
	.el-breadcrumb{
		margin-bottom: 15px;
	}
	.el-card{
		box-shadow: 0 1px 1px rgba(0,0,0,0.15) !important;
	}
</style>
