<script>
    export default{
        name:'mycheckquestion',

        created(){
            this.getusermsg();
        },

        methods: {
            goBack() {
                this.$router.go(-1)
                // console.log('go back');
            },

            async getusermsg(){
			const {data:res} = await this.axios.post('http://10.122.215.247:8080/que/getinfo',{qid:this.getqid})
			// console.log(1)
			this.qidform=res.data
			this.tableData[0] = this.qidform
			this.$set(this.tableData,0,this.qidform)
			},
        },
        data(){
            return{

                getqid:this.$route.query.qid,

                qidform:{
                    qid:this.$route.query.qid,
                    qname:'',
                    description:'',
                    fullmark:0,
                    testnum:0,
                    inputformat:'',
                    outputformat:'',
                    samplein:'',
                    sampleout:'',
                    data:[
                        {
                            intput:'',
                            output:'',
                        }
                    ]
                } ,
                tableData: [{qidform:this.qidform}]


            }
        }
    }

</script>

<style scoped>
#mycheckquestion{
