<template>
	<div>
		<el-card >
		  <div slot="header" class="clearfix">
		    <span>课程</span>
		    <el-button style="float: right; padding: 3px 0" type="text" @click="add()">添加课程</el-button>
		  </div>
		
		<el-table :data="qlist" border stripe>
			<el-table-column label="题目id" prop="qid"></el-table-column>
			<el-table-column label="题目名称" prop="qname"></el-table-column>
			<el-table-column label="最高分" prop="max"></el-table-column>
			<el-table-column label="是否完成" prop="isdone"></el-table-column>
			<el-table-column label="查看题目">
				<template slot-scope="scope">
					<el-tooltip effect="dark" content="查看题目" placement="top" :enterable="false">
				<el-button type="primary" size="mini" @click="detailquestion(scope.row.qid)">题目详细</el-button>
					</el-tooltip>			
					
					</template>
				
			</el-table-column>
		</el-table>
		</el-card>
		</div>
</template>

<script>
	export default {
		name:'questionlist',
		data(){
			return{
				qlist:[]				
			}
		},
		computed: {
			cid(){
				return this.$store.state.cid; 
			},
			uid(){
				return this.$store.state.uid
			}
			
		},
		
		methods:{
			async getquestion(){
				const {data:res} = await this.$http.post('http://10.122.215.247:8080/que/getbyc',{cid:this.cid,uid:this.uid})
				this.qlist=res.data	
			},
		detailquestion(qid){
				this.$store.commit('detailqid',qid)
				console.log('...............')
				console.log(this.$store.state.qid)
				this.$router.push('checkquestion')
			 }
		},
		created(){
			this.getquestion() 
		},
		
	}
</script>

